.Notes {
    width: 100%;
}
.NoteList {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #2F2D2D;
}
/* .NoteList:last-child {
    border: none;
} */
.Contents h2 {
    font-family: 'Apple SD Gothic Neo';
    font-weight: 500;
    font-size: 28px;
    line-height: 37px;
    color: #2F2D2D;
    position: relative;
    margin: 48px 0 0 22px;
}
.Contents h2:before {
    content:'';
    background: url(./svg/hashtag.svg);
    width: 28px;
    height: 28px;
    position: absolute;
    left: -48px;
    top: 5px;
}
.Contents p {
    font-family: 'Apple SD Gothic Neo';
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    color: #BABABA;
    margin: 22px 0 48px 25px;
    width: 455px;
}
.ListBtn {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    padding: 48px 18px;
}
.ListBtn a {
    display: inline-block;
    width: 20px;
    height: 20px;
    text-indent: -99em;
    background: url(./svg/view-btn.svg);
}
.ListBtn span {
    display: inline-block;
    font-family: 'Helvetica Neue';
    font-weight: 400;
    font-size: 14px;
    color: #595959;
    letter-spacing: 0.02em;
}