.View {
    width: 50vw;
    background: #FFFDFA;
    padding: 47px 40px;
}
.View b {
    font-family: "Helvetica Neue";
    font-weight: 200;
    font-size: 55px;
    line-height: 1;
    letter-spacing: 0.01em;
    color: #595959;
    position: relative;
    margin: 265px 0 0 40px;
    display: inline-block;
}
.View b:after {
    content: '';
    position: absolute;
    left: 5px;
    right: 8px;
    bottom: 14px;
    height: 2px;
    background: #595959; 
}
.Write {
    /* display: flex;
    flex-direction: column;
    gap: 40px; */
    justify-content: space-between;
    border-top: 1px solid #2f2d2d;
    border-bottom: 1px solid #2f2d2d;
    margin: 35px 0 90px;
    padding: 20px 0;
}
.Write input {
    font-size: 54px;
    line-height: 70px;
    font-family: 'Apple SD Gothic Neo';
    font-weight: 300;
    letter-spacing: 0.01em;
    border: none;
}
.Write textarea {
    border: none;
    resize: none;
    width: 40vw;
    height: 340px;
    background: transparent;
    font-family: 'Apple SD Gothic Neo';
    font-weight: 300;
    font-size: 14px;
    line-height: 32px;
    letter-spacing: 0.01em;
}
.Write button {
    border: none;
    padding: 12px 18px;
    background: #4670AE;
    border-radius: 30px;
    color: #fff;
    letter-spacing: 0.02em;
    font-family: 'Helvetica Neue';
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
}
.Write input:focus,
.Write textarea:focus {
    outline: none;
}
.View p {
    text-align: center;
    font-family: 'Helvetica Neue';
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    letter-spacing: 0.02em;
    color: #4670AE;
}
