.Banner {
    padding: 70px 34px 23px 59px;
    display: flex;
    justify-content: space-between;
    border-top: 1px solid #2f2d2d;
    border-bottom: 1px solid #2f2d2d;
}
.Banner .Title h1 {
    font-family: 'Helvetica Neue';
    font-weight: 400;
    color: #4670AE;
    font-size: 108px;
    line-height: 160px;
    letter-spacing: 0.005em;
}
.Banner .Menu {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 50px;
    margin-top: 25px;
}
.Banner .Menu > img {
    width: 50px;
    height: 50px;
    cursor: pointer;
}
.Banner .Menu > div > span {
    font-family: 'Helvetica Neue';
    font-weight: 400;
    font-size: 59px;
    line-height: 70px;
    color: #2F2D2D;
}
.Banner .Menu > div > img {
    height: 111px;
    width: 65px;
    margin-right: 20px;
}